import axios from 'axios';
const HOST = '';
const SUFFIX = '';
export default {
  // 公司简介
  company: {
    getContent(opt) {
      return axios.post(HOST + '/weixin/profile/index' + SUFFIX, opt);
    }
  },
  // 产品
  product: {
    // 产品名称
    getName(opt) {
      return axios.post(HOST + '/weixin/product/lists' + SUFFIX, opt);
    },
    // 产品列表
    getList(opt) {
      return axios.post(HOST + '/weixin/product/investLists' + SUFFIX, opt);
    },
    // 产品详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/product/detail' + SUFFIX, opt);
    },
    // 报告详情
    getReport(opt) {
      return axios.post('xxxx', opt);
    },
    // 历史报告
    getHistoryReport(opt) {
      return axios.post(HOST + '/weixin/product/reportLists' + SUFFIX, opt);
    }
  },
  // 新闻
  news: {
    // 新闻列表
    getList(opt) {
      return axios.post(HOST + '/weixin/news/lists' + SUFFIX, opt);
    },
    // 新闻详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/news/detail' + SUFFIX, opt);
    },
    // 发送评论
    sendMsg(opt) {
      return axios.post(HOST + '/weixin/news/comment' + SUFFIX, opt);
    },
    // 回复评论
    replyComment(opt) {
      return axios.post(HOST + '/weixin/news/reply' + SUFFIX, opt);
    },
    // 获取回复内容
    getReply(opt) {
      return axios.post(HOST + '/weixin/news/readReply' + SUFFIX, opt);
    },
    // 获取评论
    getComments(opt) {
      return axios.post(HOST + '/weixin/news/readComment' + SUFFIX, opt);
    }
  },
  // 通知
  notice: {},
  // 投票
  vote: {},
  // 调研
  survey: {
    // 调研列表
    getList(opt) {
      return axios.post(HOST + '/weixin/survey/list' + SUFFIX, opt);
    },
    // 调研详情
    getDetail(opt) {
      return axios.post(HOST + '/weixin/survey/detail' + SUFFIX, opt);
    }
  }
};



