<template>
  <div class="wrapper">
    <div class="content">{{content}}</div>
  </div>
</template>

<script>
export default {
  props: {
    content: {
      type: String,
      default: '提示'
    }
  }
};
</script>

<style lang="stylus" scoped>
.wrapper
  position fixed
  top 50%
  left 50%
  width 3rem
  min-height 1rem
  padding 0.03rem 0.05rem;
  border-radius 0.1rem
  transform translate3d(-50%, -50%, 0)
  display flex
  justify-content center
  z-index 100
  background-color rgba(0, 0, 0, 0.4)
  font-size 0
  .content
    align-self center
    text-align center
    font-size 0.34rem
    color #fff
</style>



