<template>
  <div class="survey-detail">
    <div class="detail-main">
      <p class="title">{{surveyDetail.title}}</p>
      <div class="time-author">
        <span>{{surveyDetail.time}}</span>
        <span>作者</span>
      </div>
      <div class="detail-content">{{surveyDetail.description}}</div>
    </div>
    <form class="question-form">
      <ol type="1" class="question-list">
        <li class="item" data-type="radio">
          <p class="title" data-index="1">标题标题标题标</p>
          <ol type="A" class="options">
            <li><input name="x" value="1" type="radio" id="a">
              <label for="a">经常</label>
            </li>
            <li><input name="x" value="2" type="radio" id="b">
              <label for="b">偶尔</label>
            </li>
            <li><input name="x" value="3" type="radio" id="c">
              <label for="c">从不</label>
            </li>
          </ol>
        </li>
        <li class="item" data-type="multi">
          <p class="title" data-index="2">标题标题标题标</p>
          <ol type="A" class="options">
            <li><input name="y" value="1" type="checkbox" id="e">
              <label for="e">中国</label>
            </li>
            <li><input name="y" value="2" type="checkbox" id="f">
              <label for="f">美国</label>
            </li>
            <li><input name="y" value="3" type="checkbox" id="g">
              <label for="g">英国</label>
            </li>
          </ol>
        </li>
        <li class="item" data-type="short">
          <p class="title" data-index="3">标题标题标题标</p>
          <textarea name="" id="" cols="30" rows="5"></textarea>
        </li>
      </ol>
      <div class="btn">
        <button type="submit">提交</button>
      </div>
    </form>
  </div>
</template>

<script>
import service from '@/service';
export default {
  data() {
    return {
      surveyDetail: {}
    };
  },
  created() {
    service.survey
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        this.surveyDetail = res.data.data;
      })
      .catch(err => {
        console.log(err);
      });
  },
  methods: {}
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base";
.survey-detail
  font-size 0
  .detail-main
    padding 0.2rem
    margin-bottom 0.2rem
    background-color #fff
    box-shadow 0 2px 8px 1px #ddd
    .title
      margin-bottom 0.2rem
      font-size 0.48rem
      text-align justify
    .time-author
      font-size 0.3rem
      color #777
      span
        margin-right 0.5rem
    .detail-content
      padding 0.3rem 0
      font-size 0.34rem
  .question-form
    .question-list
      .item
        font-size 0.34rem
        margin-bottom 0.2rem
        padding 0.15rem
        box-shadow 0 2px 8px 1px #ddd
        background-color #fff
        .title
          &::before
            content attr(data-index) "、"
        textarea
          width 6.57rem
          margin-top 0.2rem
          margin-left 0.38rem
          border 0.01rem solid #ccc
          resize none
        .options
          padding-left 1.4rem
          li
            margin 0.17rem 0
            input[type="radio"], input[type="checkbox"]
              display none
              &+label
                &::before
                  content ""
                  position absolute
                  margin-top 0.05rem
                  left 0.54rem
                  width 0.34rem
                  height 0.34rem
                  padding 0.02rem
                  border 0.01rem solid #aaa
                  border-radius 50%
              &:checked+label
                &::before
                  background-color $color-main
                  background-clip content-box
            input[type="checkbox"]
              &+label
                &::before
                  padding 0
                  border-radius 0
              &:checked+label
                &::before
                  content "\2713"
                  background-color transparent
                  line-height 0.38rem
                  font-style italic
                  font-weight bold
                  font-size 0.4rem
                  color $color-main
    .btn
      margin 0.5rem 0 0.8rem
      text-align center
      button
        width 4rem
        height 0.8rem
        border none
        border-radius 0.05rem
        background-color @color-main
        color #fff
        font-size 0.3rem
</style>



