<template>
  <div class="news-detail">
    <!-- 新闻内容 -->
    <scroll-view class="scroll-wrapper" :data="commentsList" :pullup="pullup" @scrollToEnd="getMoreComments">
      <div class="scroll-content">
        <div class="detail-main">
          <p class="title">{{newsDetail.title}}</p>
          <div class="time-author">
            <span>{{newsDetail.timestamp}}</span>
            <span>{{newsDetail.author}}</span>
          </div>
          <div class="abstract">{{newsDetail.abstract}}</div>
          <div class="detail-content" v-html="newsDetail.content"></div>
        </div>
        <!-- 评论 -->
        <div class="detail-comment">
          <ul class="divide-line">
            <li></li>
            <li>评论</li>
            <li></li>
          </ul>
          <div class="leave-comment">
            <a href="javascript:" class="edit" v-show="!showLeaveMsg&&!isAdmin" @click="showInput">
              <img src="../../assets/icon-edit.png" alt="icon">
              <span>评论</span>
            </a>
            <div class="form" v-show="showLeaveMsg">
              <form onsubmit="return false">
                <div class="input" contenteditable="true" @blur="hideInput" @input="getText" ref="textarea"></div>
                <button type="submit" @click="sendMsg">发送</button>
              </form>
            </div>
          </div>
          <ul class="comments">
            <li class="item" v-for="item in commentsList" :key="item.id">
              <div class="avatar">
                <img :src="item.avatar" alt="头像">
              </div>
              <div class="text">
                <span class="nickname">{{item.name}}</span>
                <p class="message">{{item.content}}</p>
                <span class="time">{{item.timestamp}}</span>
                <div class="reply-btn" v-show="isAdmin">
                  <a href="javascript:" @click="showReplyInput(item.id, $event)">回复</a>
                </div>
                <!-- 回复列表 -->
                <div class="reply-list" v-show="item.reply_count">
                  <ul>
                    <li v-for="(reply, index) in replyObj[item.id] || []" :key="index">
                      <div class="avatar">
                        <img :src="reply.avatar" alt="头像">
                      </div>
                      <div class="text">
                        <span class="nickname">{{reply.name}}</span>
                        <p class="message">{{reply.content}}</p>
                        <span class="time">{{reply.timestamp}}</span>
                      </div>
                    </li>
                  </ul>
                  <a href="javascript:" v-show="!(replyArgs[item.id]&&replyArgs[item.id].over)" @click="getReply(item.id)">{{replyArgs[item.id]&&replyArgs[item.id].tip || '查看回复'}}</a>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <loading-tip :tip="loadingTip"></loading-tip>
      </div>
    </scroll-view>
    <transition name="fade">
      <toast-tip v-show="activeTips" :content="tips" />
    </transition>
    <form onsubmit="return false" class="form-reply" v-show="showReply">
      <div class="input" contenteditable="true" ref="replyInput" @input="getReplyTxt" @blur="hideReplyInput">dfsfsd</div>
      <button type="submit" @click="replyComment">确定</button>
    </form>
  </div>
</template>

<script>
import service from '@/service';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
import ToastTip from '@/components/tips/ToastTip';
export default {
  data() {
    return {
      isAdmin: true, // 当前角色类别
      pullup: true, // 允许上拉加载
      commentObj: { avatar: '', name: '', content: '', timestamp: '刚刚' }, // 评论的json数据
      newsDetail: {}, // 新闻详情
      timeHandle: 0, // 定时器的句柄
      tips: '', // 提示内容
      activeTips: false, // 是否显示提示
      showLeaveMsg: false, // 是否显示评论
      msgContent: '', // 评论输入的内容
      commentsArg: { offset: 0, limit: 10, id: this.$route.params.id }, // 获取评论的参数
      commentsList: [], // 保存评论的数组
      loadingTip: '', // 加载提示
      showReply: false, // 显示回复框
      replyMsg: { news_id: this.$route.params.id, comment_id: 0, content: '' },
      replyObj: {}, // 回复的对象
      replyArgs: {} // 获取回复参数
    };
  },
  created() {
    // 获取新闻详情
    service.news
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        if (res.data.code === 200) {
          this.newsDetail = res.data.data.detail;
          // 保存当前用户信息
          this.commentObj.avatar = res.data.data.detail.avatar;
          this.commentObj.name = res.data.data.detail.name;
          // this.isAdmin = res.data.data.detail.isAdmin;
        } else {
          this.showTips(res.data.msg);
        }
      })
      .catch(err => {
        console.err(err);
      });

    // 获取新闻评论
    this.queryComments();
  },
  methods: {
    // 隐藏评论框
    hideInput() {
      if (!this.$refs.textarea.innerText) {
        this.showLeaveMsg = false;
      }
    },
    showInput() {
      this.showLeaveMsg = true;
      const that = this;
      setTimeout(function() {
        that.$refs.textarea.focus();
      }, 100);
    },
    // 获取评论内容
    getText() {
      const that = this;
      clearTimeout(this.timeHandle);
      this.timeHandle = setTimeout(function() {
        that.msgContent = that.$refs.textarea.innerText;
      }, 200);
    },
    // 发送评论
    sendMsg(event) {
      if (this.msgContent.trim().length > 3 || event._constructed) {
        service.news
          .sendMsg({ news_id: this.$route.params.id, content: this.msgContent })
          .then(res => {
            if (res.data.code === 200) {
              this.showLeaveMsg = false;
              this.$refs.textarea.innerText = '';
              this.showTips('评论已发送');
              this.commentObj.content = this.msgContent;
              this.commentObj.id = res.data.data.id;
              res.data.data.id && this.commentsList.unshift(this.commentObj);
            }
          })
          .catch(err => {
            console.err(err);
          });
      } else {
        this.showTips('评论内容至少3个字评论内容至少3个字评论内容至少3个字评论内容至少3个字');
      }
    },
    // 显示回复框
    showReplyInput(id, event) {
      const that = this;
      if (event._constructed) {
        this.showReply = true;
        this.replyMsg.comment_id = id;
        setTimeout(function() {
          that.$refs.replyInput.focus();
        }, 100);
      }
    },
    // 获取评论输入
    getReplyTxt() {
      const that = this;
      clearTimeout(this.timeHandle);
      this.timeHandle = setTimeout(function() {
        that.replyMsg.content = that.$refs.replyInput.innerText;
      }, 200);
    },
    // 回复评论
    replyComment() {
      service.news
        .replyComment(this.replyMsg)
        .then(res => {
          if (res.data.code === 200) {
            this.showReply = false;
            this.$refs.replyInput.innerText = '';
            this.showTips('回复已发送');
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取回复
    getReply(id) {
      if (this.replyArgs[id]) {
        if (this.replyArgs[id].over) {
          return;
        }
        this.replyArgs[id].offset += this.replyArgs[id].limit;
      } else {
        this.replyArgs[id] = { id: id, offset: 0, limit: 5 };
      }
      service.news
        .getReply(this.replyArgs[id])
        .then(res => {
          if (res.data.code === 200) {
            if (this.replyObj[id]) {
              this.replyObj[id].push(...res.data.data.list);
            } else {
              this.$set(this.replyObj, id, res.data.data.list);
            }
            if (res.data.data.list.length === this.replyArgs[id].limit) {
              this.replyArgs[id].tip = '加载更多评论';
            } else {
              this.replyArgs[id].over = true;
            }
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 隐藏回复框
    hideReplyInput() {
      if (!this.$refs.replyInput.innerText) {
        this.showReply = false;
      }
    },
    // 显示提示
    showTips(txt) {
      this.tips = txt;
      this.activeTips = true;
      const that = this;
      setTimeout(function() {
        that.activeTips = false;
      }, 1500);
    },
    // 查询评论
    queryComments() {
      if (this.commentsArg.over) {
        return;
      }
      this.loadingTip = '正在加载中';
      service.news
        .getComments(this.commentsArg)
        .then(res => {
          if (res.data.code === 200) {
            this.commentsList.push(...res.data.data.list);
            if (!this.commentsArg.offset && !res.data.data.list.length) {
              this.loadingTip = '暂无数据';
              this.commentsArg.over = true;
            } else if (res.data.data.list.length < this.commentsArg.limit) {
              this.loadingTip = '全部加载完毕';
              this.commentsArg.over = true;
            } else {
              this.loadingTip = '释放加载更多';
            }
          } else {
            this.loadingTip = '加载失败';
            this.showTips(res.data.msg);
          }
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 获取更多评论
    getMoreComments() {
      this.commentsArg.offset += this.commentsArg.limit;
      this.queryComments();
    }
  },
  components: {
    ScrollView,
    LoadingTip,
    ToastTip
  }
};
</script>
<style lang="stylus" scoped>
@import "../../common/stylus/base"

/* 动画 */
.fade-enter-active, .fade-leave-active
  transition opacity 0.6s
.fade-enter, .fade-leave-to
  opacity 0
.news-detail
  position absolute
  top 0
  left 0
  bottom 0
  right 0
  font-size 0
  /* 新闻 */
  .scroll-wrapper
    position absolute
    width 100%
    height 100%
    overflow hidden
    .detail-main
      padding 0.2rem
      margin-bottom 0.3rem
      background-color #fff
      box-shadow 0 2px 8px 1px #ddd
      .title
        margin-bottom 0.2rem
        font-size 0.48rem
      .time-author
        font-size 0.32rem
        color #777
        span
          margin-right 0.5rem
      .detail-content
        padding 0.3rem
        font-size 0.36rem
        word-break break-all
      .abstract
        font-size 0.34rem
        color #666
    /* 评论 */
    .detail-comment
      padding 0 0.4rem
      .divide-line
        display flex
        margin-bottom 0.2rem
        li
          flex-shrink 1
          align-self center
          font-size 0.34rem
          &:nth-child(2)
            margin 0 0.2rem
          &:first-child, &:last-child
            flex-grow 1
            border-bottom 0.01rem solid #ccc
      .leave-comment
        text-align right
        .edit
          display inline-block
          img
            width 0.36rem
            height 0.4rem
            margin-right 0.08rem
          span
            font-size 0.26rem
        form
          display flex
          margin 0 -0.4rem
          padding 0.12rem
          background-color #fff
          .input
            flex 1
            min-height 0.5rem
            border-bottom 1px solid $color-main
            margin 0 0.15rem
            padding 0.12rem 0
            outline none
            font-size 0.3rem
            text-align justify
            caret-color $color-main
          button
            width 1rem
            height 0.55rem
            border none
            border-radius 0.08rem
            align-self flex-end
            background-color $color-main
            color #fff
      .comments
        .item
          display flex
          margin-bottom 0.3rem
          .avatar
            flex-shrink 0
            img
              width 0.88rem
              height 0.88rem
              background-color #ccc
          .text
            flex-grow 1
            margin-left 0.33rem
            .nickname
              margin-bottom 0.1rem
              font-size 0.3rem
            .message
              margin-bottom 0.1rem
              text-align justify
              font-size 0.34rem
              color #444
            .time
              font-size 0.28rem
              color #999
            .reply-btn
              text-align right
              a
                font-size 0.32rem
                color lighten($color-link, 30%)
            .reply-list
              &>ul>li
                display flex
                margin-bottom 0.12rem
                .avatar
                  flex-shrink 0
                .text
                  flex-grow 1
              a
                font-size 0.28rem
                color lighten($color-link, 30%)
  .form-reply
    display flex
    position absolute
    bottom 0
    width 100%
    padding-right 0.12rem
    background-color #fff
    .input
      box-shadow 0 -0.01rem 0.08rem 0.02rem #eee
      flex-grow 1
      padding 0.12rem 0.08rem
      border-bottom 0.03rem solid $color-main
      margin 0 0.15rem
      text-align justify
      font-size 0.34rem
      outline none
    button
      align-self center
      width 1rem
      height 0.55rem
      border none
      border-radius 0.05rem
      background-color $color-main
      color #fff
</style>



