<template>
  <scroll-view class="survey-list-wrapper" :data="surveyList" :pullup="pullup" @pullup="getMoreSurvey">
    <ul class="survey-list">
      <li class="item" v-for="item in surveyList" :key="item.id">
        <router-link :to="'/survey/'+item.id+'/detail'">
          <div class="cover">
            <img src="" alt="封面">
          </div>
          <div class="brief">
            <span class="title">{{item.title}}</span>
            <span class="author">{{item.author}}</span>
          </div>
          <div class="stamp">
            <span class="time">{{item.time}}</span>
            <span class="status" :status="item.status">{{item.status|status}}</span>
          </div>
        </router-link>
      </li>
    </ul>
  </scroll-view>
</template>

<script>
import service from '@/service';
import ScrollView from '@/components/scroll/ScrollView';
export default {
  data() {
    return {
      surveyArg: { limit: 10, offset: 0, over: false }, // 获取调研的参数
      surveyList: [], // 调研列表
      pullup: true // 上拉刷新
    };
  },
  created() {
    this.querySurvey();
  },
  computed: {},
  methods: {
    // 获取调研列表
    querySurvey() {
      if (this.querySurvey.over) {
        alert('over');
        return;
      }
      service.survey.getList(this.surveyArg).then(res => {
        this.surveyList.push(...res.data.data);
        if (this.surveyList.limit > this.surveyList.length) {
          this.surveyArg.over = true;
        }
      });
    },
    // 上拉获取更多调研
    getMoreSurvey() {
      this.querySurvey.offset += 10;
      this.querySurvey();
    }
  },
  filters: {
    status(type) {
      switch (Number(type)) {
        case 1:
          return '已结束';
        case 2:
          return '已投票';
        case 3:
          return '暂停';
      }
    }
  },
  components: {
    ScrollView
  }
};
</script>
<style scoped lang="stylus">
@import "../../common/stylus/base"

.survey-list-wrapper
  position absolute
  width 100%
  height 100%
  background-color #efefef
  font-size 0
  .survey-list
    padding 0.27rem 0.24rem
    .item
      &>a
        background-color #fff
        display flex
        padding 0.1rem
        margin-bottom 0.2rem
        .cover
          margin-right 0.1rem
          img
            width 1rem
            height 1rem
            background-color #ccc
        .brief
          display flex
          flex-direction column
          justify-content space-between
          flex-grow 1
          .title
            overflow-x hidden
            white-space nowrap
            text-overflow ellipsis
            font-size 0.34rem
          .author
            font-size 0.32rem
            color #999
        .stamp
          display flex
          flex-direction column
          justify-content space-between
          .time
            font-size 0.32rem
            color #999
          .status
            font-size 0.32rem
            text-align right
            &[status="1"]
              color #2dc56d
            &[status="2"]
              color #d62525
            &[status="3"]
              color #f38534
            &[status="4"]
              color #2dc56d
            &[status="5"]
              color $color-main
</style>




