<template>
  <div class="product-list">
    <!-- 下拉列表 -->
    <div class="drop-list">
      <p class="selected" @click="showList=true">
        <a href="javascript:">{{productSelected.title}}</a>
      </p>
      <transition name="slide">
        <ul class="select-list" v-show="showList">
          <li :class="{active: productSelected.id === 0}">
            <a href="javascript:" @click="getProduct({id: 0, title: '全部产品'})">全部产品</a>
          </li>
          <li v-for="item in productName" :key="item.id" :class="{active: productSelected.id === item.id}">
            <a href="javascript:" @click="getProduct(item)">{{item.title}}</a>
          </li>
        </ul>
      </transition>
    </div>
    <!-- 产品列表 -->
    <scroll-view class="product-list-wrapper" :data="productList" :pullup="pullup" @scrollToEnd="getMoreProduct">
      <div class="scroll-content">
        <ul class="product-list">
          <li class="item" v-for="product in productList" :key="product.id">
            <router-link :to="'/product/'+product.id+'/detail'">
              <div class="top">
                <div class="left">
                  <span>产品名称：</span>
                  <span class="title">{{product.title}}</span>
                </div>
                <div class="right">{{product.time}}</div>
              </div>
              <div class="bottom">
                <span>投资金额：</span>
                <span class="currency">{{product.money}}</span>
              </div>
            </router-link>
          </li>
        </ul>
        <loading-tip :tip="loadingTip"></loading-tip>
      </div>
    </scroll-view>
    <transition name="fade">
      <div class="mask" v-show="showList" @click="showList=false"></div>
    </transition>
  </div>
</template>

<script>
import service from '@/service';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
export default {
  name: 'ProductList',
  data() {
    return {
      productList: [], // 产品列表数组
      productName: [], // 产品名称数组
      productSelected: { id: 0, title: '全部产品' }, // 选中的产品
      queryArg: { id: 0, limit: 10, offset: 0 }, // 产品查询参数
      showList: false, // 显示下拉列表
      pullup: true, // 允许上拉加载更多,
      loadingTip: ''
    };
  },
  created() {
    service.product
      .getName()
      .then(res => {
        this.productName = res.data.data.list;
      })
      .catch(err => {
        console.log(err);
      });
    this.queryProduct();
  },
  mounted() {},
  computed: {},
  methods: {
    // 服务器获取产品列表
    queryProduct() {
      if (this.queryArg.over) {
        return;
      }
      this.loadingTip = '正在加载中';
      service.product
        .getList(this.queryArg)
        .then(res => {
          this.productList.push(...res.data.data.list);
          if (!this.queryArg.offset && !res.data.data.list.length) {
            this.loadingTip = '暂无数据';
            this.queryArg.over = true;
          } else if (res.data.data.list.length < this.queryArg.limit) {
            this.loadingTip = '全部加载完毕';
            this.queryArg.over = true;
          } else {
            this.loadingTip = '释放加载更多';
          }
        })
        .catch(err => {
          this.loadingTip = '加载失败';
          console.log(err);
        });
    },
    // 下拉列表过滤产品
    getProduct(item) {
      if (item.id === this.productSelected.id) {
        return;
      }
      this.showList = false;
      this.productSelected = item;
      this.queryArg = { id: item.id, offset: 0, limit: 10 };
      this.loadingTip = '';
      this.productList = [];
      this.queryProduct();
    },
    // 上拉加载更多
    getMoreProduct() {
      this.queryArg.offset += this.queryArg.limit;
      this.queryProduct();
    }
  },
  components: {
    ScrollView,
    LoadingTip
  }
};
</script>

<style scoped lang="stylus">
@import "../../common/stylus/base"

// 动画
.slide-enter-active, .slide-leave-active
  transition transform 0.2s
  transform translate3d(0, 0, 0)
.slide-enter, .slide-leave-to
  transform translate3d(0, -100%, 0)
.fade-enter-active, .fade-leave-active
  transition opacity 0.5s
.fade-enter, .fade-leave-to
  opacity 0
// main
.product-list
  background-color #efefef
  font-size 0
  // 产品下拉列表
  .drop-list
    position relative
    width 100%
    height 0.8rem
    background-color #fff
    z-index 100
    text-align center
    line-height 0.8rem
    .selected
      position relative
      background-color #fff
      box-shadow 0 0.01rem 0.05rem 0.03rem #eee
      z-index 20
      a
        display block
        height 0.8rem
        font-size 0.3rem
        &::before
          content " "
          position absolute
          top 0.28rem
          right 0.25rem
          border-left 0.22rem solid transparent
          border-right 0.22rem solid transparent
          border-top 0.24rem solid #aaa
        &::after
          content " "
          position absolute
          top 0.23rem
          right 0.25rem
          border-left 0.22rem solid transparent
          border-right 0.22rem solid transparent
          border-top 0.24rem solid #fff
    .select-list
      background-color #fff
      li
        a
          display block
          height 0.8rem
          border-bottom 0.01rem solid #eee
          font-size 0.3rem
        &.active, &:hover
          background-color $color-main
          a
            color #fff
  // 产品列表
  .product-list-wrapper
    position absolute
    width 100%
    height calc(100% - 0.8rem)
    padding-top 0.15rem
    background-color #efefef
    overflow hidden
    .scroll-content
      position absolute
      width 100%
      .product-list
        position relative
        padding-bottom 0.15rem
        z-index 10
        .item
          margin-bottom 0.17rem
          box-shadow 0 3px 5px 1px #ddd
          a
            display flex
            flex-direction column
            padding 0.3rem
            background-color #fff
            font-size 0.34rem
            .top
              display flex
              justify-content space-between
              margin-bottom 0.4rem
              .title
                word-break break-all
              .right
                flex-shrink 0
                color #999
            .bottom
              .currency
                color $color-main
.mask
  position fixed
  width 100%
  height 100%
  background-color $color-toast
  touch-action none
</style>



