<template>
  <div class="company-profile" ref="wrapper">
    <div class="content" v-html="companyProfile"></div>
  </div>
</template>

<script>
import service from '@/service';
import BScroll from 'better-scroll';
export default {
  name: 'Company',
  data() {
    return {
      companyProfile: ''
    };
  },
  created() {
    service.company
      .getContent()
      .then(res => {
        if (res.data.code === 200) {
          this.companyProfile = res.data.data.content;
          this.$nextTick(() => {
            this.scroll = new BScroll(this.$refs.wrapper, { click: true });
          });
        } else {
          console.error(res.data.msg);
        }
      })
      .catch(err => {
        console.error(err);
      });
  }
};
</script>

<style scoped lang="stylus">
.company-profile
  position absolute
  top 0
  bottom 0
  left 0
  right 0
  .content
    padding 0.2rem
    text-align justify
    font-size 0.34rem
    h1
      font-size 0.48rem
</style>



