<template>
  <scroll-view class="scroll-wrapper" :data="newsList" :pullup="pullup" @scrollToEnd="getMoreNews">
    <div class="scroll-content">
      <ul class="news-list">
        <li class="item" v-for="item in newsList" :key="item.id">
          <router-link :to="'/news/'+ item.id +'/detail'">
            <div class="text">
              <p class="title">{{item.title}}</p>
              <span class="time">{{item.timestamp}}</span>
            </div>
            <div class="cover">
              <img :src="item.imgurl" alt="cover">
            </div>
          </router-link>
        </li>
      </ul>
      <loading-tip :tip="loadingTip"></loading-tip>
    </div>
  </scroll-view>
</template>
<script>
import service from '@/service';
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
export default {
  data() {
    return {
      newsList: [], // 新闻列表数组
      queryArg: { id: 0, limit: 10, offset: 0, type: this.$route.params.type }, // 新闻查询参数
      pullup: true, // 允许上拉加载更多,
      loadingTip: ''
    };
  },
  created() {
    // this.queryArg.type = ;
    this.queryNews();
  },
  methods: {
    // 服务器获取新闻列表
    queryNews() {
      if (this.queryArg.over) {
        return;
      }
      this.loadingTip = '正在加载中';
      service.news
        .getList(this.queryArg)
        .then(res => {
          if (res.data.code === 200) {
            this.newsList.push(...res.data.data.list);
            if (!this.queryArg.offset && !res.data.data.list.length) {
              this.loadingTip = '暂无数据';
              this.queryArg.over = true;
            } else if (res.data.data.list.length < this.queryArg.limit) {
              this.loadingTip = '全部加载完毕';
              this.queryArg.over = true;
            } else {
              this.loadingTip = '释放加载更多';
            }
          } else {
            console.error(res.data.msg);
          }
        })
        .catch(err => {
          this.loadingTip = '加载失败';
          console.log(err);
        });
    },

    // 上拉加载更多
    getMoreNews() {
      this.queryArg.offset += this.queryArg.limit;
      this.queryNews();
    }
  },
  components: {
    ScrollView,
    LoadingTip
  }
};
</script>

<style lang="stylus" scoped>
@import "../../common/stylus/base"

.scroll-wrapper
  position absolute
  width 100%
  height 100%
  background-color #efefef
  overflow hidden
  font-size 0
  .scroll-content
    .news-list
      padding 0.3rem
      background-color #fff
      /* 列表项 */
      .item
        border-bottom 0.01rem solid #ccc
        padding 0.2rem 0
        &>a
          display flex
          justify-content space-between
          .text
            .title
              margin-bottom 0.2rem
              word-break break-all
              font-size 0.34rem
              color #000
            .time
              font-size 0.3rem
              color #777
          .cover
            flex-shrink 0
            img
              width 1rem
              height 1rem
              background-color #ccc
</style>



