<template>
  <div class="loading-wrapper">
    <span>{{tip}}</span>
  </div>
</template>

<script>
export default {
  props: {
    tip: {
      type: String,
      default: ''
    }
  }
};
</script>
<style lang="stylus" scoped>
.loading-wrapper
  position absolute
  bottom -1rem
  width 100%
  text-align center
  span
    padding 0 0.05rem
    border-radius 0.05rem
    background-color rgba(0,0,0,.3)
    color #ffffff
    font-size 0.3rem
</style>




