<template>
  <div class="product-detail hide">
    <div class="header">
      <div class="invest-sum">
        <span>投资金额：</span>
        <span class="currency">{{productDetail.money}}</span>
      </div>
      <router-link :to="'/product/'+productDetail.id+'/report/history'" class="link">查看报告</router-link>
    </div>
    <div class="section">
      <ul>
        <li>
          <span>产品状态：</span>
          <span>{{status}}</span>
        </li>
        <li>
          <span>成立日期：</span>
          <span>{{productDetail.establish}}</span>
        </li>
        <li>
          <span>管理人：</span>
          <span>{{productDetail.manager}}</span>
        </li>
        <li>
          <span>产品期限：</span>
          <span>{{productDetail.deadline}}</span>
        </li>
        <li>
          <span>管理机构：</span>
          <span>{{productDetail.trusteeship}}</span>
        </li>
        <li>
          <span>投资范围属性：</span>
          <span>{{productDetail.scope}}</span>
        </li>
      </ul>
    </div>
    <div class="section">
      <ul>
        <li>
          <span>认购费：</span>
          <span class="currency">{{productDetail.subscription_fee}}</span>
        </li>
        <li>
          <span>赎回费：</span>
          <span class="currency">{{productDetail.redemption_fee}}</span>
        </li>
        <li>
          <span>管理费：</span>
          <span class="currency">{{productDetail.management_fee}}</span>
        </li>
        <li>
          <span>托管费：</span>
          <span class="currency">{{productDetail.trust_fee}}</span>
        </li>
        <li>
          <span>外包服务费：</span>
          <span class="currency">{{productDetail.outsourcing_fee}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import service from '@/service';
export default {
  name: 'ProductDetail',
  data() {
    return {
      productDetail: {}
    };
  },
  created() {
    service.product
      .getDetail({ id: this.$route.params.id })
      .then(res => {
        if (res.data.code === 200) {
          this.productDetail = res.data.data.info;
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  computed: {
    status() {
      switch (Number(this.productDetail.status)) {
        case 1:
          return '募集';
        case 2:
          return '存续';
        case 3:
          return '退出';
      }
    }
  }
};
</script>

<style scoped lang="stylus">
@import "../../common/stylus/base"

.product-detail
  /* 顶部信息 */
  .header
    display flex
    justify-content space-between
    height 0.8rem
    padding 0 0.3rem
    margin 0.26rem 0 0.17rem 0
    background-color #fff
    box-shadow 0 3px 5px 1px #ddd
    line-height 0.8rem
    font-size 0.34rem
    .invest-sum
      .currency
        color $color-main
    .link
      text-decoration underline
      font-size 0.3rem
      color $color-link
  /* 详细信息 */
  .section
    padding 0.25rem
    margin-bottom 0.17rem
    background-color #fff
    box-shadow 0 3px 5px 1px #ddd
    ul>li
      display flex
      margin-bottom 0.3rem
      &:last-child
        margin-bottom 0
      span
        font-size 0.32rem
        &:first-child
          flex-basis 2.5rem
          text-align right
</style>



