<template>
  <div class="report-history">
    <div class="header">
      <span>发布日期</span>
      <span>操作</span>
    </div>
    <scroll-view class="report-list-wrapper" :data="reportList" :pullup="pullup" @scrollToEnd="getMoreReport">
      <div class="scroll-content">
        <ul class="report-list">
          <li class="item" v-for="item in reportList" :key="item.file_id">
            <span>{{item.time}}</span>
            <a :href="item.url" :download="item.time+'.'+item.ext">下载报告</a>
          </li>
        </ul>
        <loading-tip :tip="loadingTip"></loading-tip>
      </div>
    </scroll-view>
  </div>
</template>

<script>
import ScrollView from '@/components/scroll/ScrollView';
import LoadingTip from '@/components/tips/LoadingTip';
import service from '@/service';
export default {
  name: 'ProductReporHistory',
  data() {
    return {
      queryArg: { id: 0, limit: 20, offset: 0, over: false },
      reportList: [],
      pullup: true,
      loadingTip: ''
    };
  },
  created() {
    this.queryArg.id = this.$route.params.id;
    this.queryReport();
  },
  methods: {
    queryReport() {
      if (this.queryArg.over) {
        return;
      }
      this.loadingTip = '正在加载中';
      service.product
        .getHistoryReport(this.queryArg)
        .then(res => {
          this.reportList.push(...res.data.data.list);
          if (!this.queryArg.offset && !res.data.data.list.length) {
            this.loadingTip = '暂无数据';
            this.queryArg.over = true;
          } else if (res.data.data.list.length < this.queryArg.limit) {
            this.loadingTip = '全部加载完毕';
            this.queryArg.over = true;
          } else {
            this.loadingTip = '释放加载更多';
          }
        })
        .catch(err => {
          this.loadingTip = '加载失败';
          console.log(err);
        });
    },
    getMoreReport() {
      this.queryArg.offset += this.queryArg.limit;
      this.queryReport();
    }
  },
  components: {
    ScrollView,
    LoadingTip
  }
};
</script>

<style scoped lang="stylus">
.report-history
  .header
    display flex
    justify-content space-between
    position relative
    padding 0.1rem 0.3rem
    background-color #fff
    box-shadow 0 0.01rem 0.05rem 0.03rem #eee
    z-index 20
    span
      font-size 0.4rem
  .report-list-wrapper
    position absolute
    width 100%
    height calc(100% - 0.4rem)
    overflow hidden
    .report-list
      padding-bottom 0.2rem
      .item
        display flex
        justify-content space-between
        padding 0.1rem 0.3rem
        font-size 0.38rem
</style>



