import Vue from 'vue';
import Router from 'vue-router';
import axios from 'axios';
import Company from '@/components/company/Company';
import ProductList from '@/components/product/ProductList';
import ProductDetail from '@/components/product/ProductDetail';
import ProductReporHistory from '@/components/product/ProductReporHistory';
import SurveyList from '@/components/survey/SurveyList';
import SurveyDetail from '@/components/survey/SurveyDetail';
import SurveyTips from '@/components/survey/SurveyTips';
import NewsList from '@/components/news/NewsList';
import NewsDetail from '@/components/news/NewsDetail';
Vue.use(Router);
const userInfo = '/aid/:aid';
// 检查是否登录
function checkLogin(to, from, next) {
  let aid = to.params.aid;
  let url = encodeURIComponent(window.location.href);
  axios
    .post('/weixin/user/isLogin')
    .then(res => {
      if (res.data.code === 402) {
        axios
          .get('/weixin/user/login/aid/' + aid + '/callback/' + url)
          .then(res => {})
          .catch(err => {
            console.error(err);
          });
      } else if (res.data.code === 200) {
        next();
      }
    })
    .catch(err => {
      alert(err);
      console.error(err);
    });
}
export default new Router({
  routes: [
    // 公司
    {
      path: '/company' + userInfo,
      name: 'Company',
      component: Company,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    // 产品
    {
      path: '/product/list' + userInfo,
      name: 'Product',
      component: ProductList,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    {
      path: '/product/:id/detail' + userInfo,
      component: ProductDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    // {
    //   path: '/product/:id/report/detail',
    //   component: ProductReportDetail
    // },
    {
      path: '/product/:id/report/history' + userInfo,
      component: ProductReporHistory,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    // 新闻
    {
      path: '/news/list/:type' + userInfo,
      component: NewsList,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    {
      path: '/news/:id/detail' + userInfo,
      component: NewsDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    // 通知
    // 投票
    // 调研
    {
      path: '/survey/list' + userInfo,
      component: SurveyList,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    {
      path: '/survey/:id/detail' + userInfo,
      component: SurveyDetail,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    },
    {
      path: '/survey/:id/tips' + userInfo,
      component: SurveyTips,
      beforeEnter: (to, from, next) => {
        checkLogin(to, from, next);
      }
    }
  ]
});



